* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: arial;
  overflow: hidden;
  user-select: none;
}

h1 {
  font-size: 16px;
  text-align: center;
  margin: 10px 0;
  color: #0088cc;
  text-transform: uppercase;
}

.app {
  height: 100vh;
}

.app-area {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  overflow: auto;
  width: calc(100% - 200px);
}

.app-panel {
  position: absolute;
  top: 0;
  right: 0;
  overflow: auto;
  width: 200px;
  height: 100%;
  border-left: 3px solid #0088cc;
}

.app-panel-elem {
  padding: 15px 10px 15px 60px;
  position: relative;
  cursor: pointer;
}

.app-panel-elem:after {
  content: '';
  display: block;
  position: absolute;
  width: 40px;
  height: 40px;
  top: 50%;
  left: 10px;
  margin-top: -20px;
  border-radius: 50%;
  background-color: #fff;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  transition: 0.3s;
  background-position: center;
  background-size: 25px 25px;
  background-repeat: no-repeat;
  box-shadow: 0 0 0 2px #0088cc;
}

.app-panel-elem__node:after {
  background-image: url(./../images/avatar.svg);
}

.app-panel-elem:hover:after {
  -webkit-transform: scale(0.9);
  -moz-transform: scale(0.9);
  -ms-transform: scale(0.9);
  -o-transform: scale(0.9);
  transform: scale(0.9);
  box-shadow: 0 0 0 4px #0088cc;
}

.app-panel-elem__draggable {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  padding: 0;
  font-size: 0;
  width: 40px;
  height: 40px;
}

.app-panel-elem__draggable:after {
  left: 0;
}

.app-panel-elem__draggable.remove {
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  transition: 0.4s;
  -webkit-transform: translate(-50%, -50%) scale(3);
  -moz-transform: translate(-50%, -50%) scale(3);
  -ms-transform: translate(-50%, -50%) scale(3);
  -o-transform: translate(-50%, -50%) scale(3);
  transform: translate(-50%, -50%) scale(3);
  opacity: 0;
  visibility: hidden;
}




